:root {
  --color-dark: #000;
  --color-light: #fff;
  --hover-color: #0693e3;
  --font-family: "Roboto", sans-serif;
}

html {
  font-size: 10px;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-family);
  font-size: 1.4rem;
  line-height: 1.5;
}

header#header {
  background-color: transparent;
  background-image: none;
}

.green-top-block {
  position: relative;
  max-width: 1100px;
  height: auto;
  padding: 10px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  background-color: var(--color-dark);
  color: var(--color-light);
}

.region-header-top-left {
  position: relative;
  padding-left: 20px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}
.region-header-top-left div {
  margin: 5px;
}

#block-green-theme-addweathernewyork {
  display: block;
  position: relative;
  font-weight: 700;
}
#block-green-theme-addweathernewyork::before {
  display: block;
  z-index: 100;
  position: absolute;
  top: 50%;
  left: -15px;
  transform: translateY(-50%);
  content: "🌤";
  overflow: hidden;
}

.region-header-top-center {
  padding: 5px;
}

.navigation.menu--topnav ul.menu {
  display: flex;
  gap: 5px;
}
.navigation.menu--topnav ul.menu li a {
  color: var(--color-light);
  transition: all 0.4s ease-in-out;
}
.navigation.menu--topnav ul.menu li a:hover {
  color: var(--hover-color);
}

.region-header-top-right {
  max-width: 30%;
  min-width: 200px;
  margin-left: auto;
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  gap: 5px;
}
.region-header-top-right .social-link-item {
  display: block;
  position: relative;
  height: 20px;
  width: 20px;
  flex-grow: 0;
  padding: 0;
  overflow: hidden;
  transition: all 0.4s ease-in-out;
}
.region-header-top-right .social-link-item:hover {
  scale: 1.2;
}
.region-header-top-right .social-link-item img {
  display: block;
  height: 20px;
  width: 20px;
  object-fit: contain;
  filter: grayscale(100%);
}
.region-header-top-right .social-link-item .field--name-field-social-link {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.green-header.region.region-header {
  position: relative;
  max-width: 1100px;
  height: auto;
  padding: 10px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}

.site-branding__logo {
  display: block;
  width: 220px;
}

.green-banner {
  position: relative;
  width: 60%;
  min-width: 470px;
  flex-shrink: 1;
}
.green-banner .green-banner__text {
  position: absolute;
  top: 10px;
  left: 10px;
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  max-width: 60%;
}
.green-banner img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: contain;
}

.header_nav {
  position: relative;
  z-index: 5;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
}

.region.region-primary-menu {
  position: relative;
  max-width: 1100px;
  height: auto;
  padding: 10px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

nav.navigation.menu--main .menu {
  width: 70%;
  min-width: 550px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  font-size: 1.6rem;
  text-transform: uppercase;
  font-weight: bold;
}
nav.navigation.menu--main .menu .menu-item a {
  display: block;
  position: relative;
}
nav.navigation.menu--main .menu .menu-item a::before {
  display: block;
  position: absolute;
  content: "";
  left: 50%;
  right: 50%;
  bottom: 0;
  height: 3px;
  background-color: var(--hover-color);
  transition: all 0.4s ease-in-out;
}
nav.navigation.menu--main .menu .menu-item a:hover::before {
  left: 0;
  right: 0;
}

.search-block-form.block-search {
  display: block;
  position: relative;
}
.search-block-form.block-search .form-search {
  display: none;
  opacity: 0;
  z-index: -1;
  position: absolute;
  font-size: 1.6rem;
  padding-left: 30px;
  padding-right: 30px;
  bottom: -100px;
  right: 0;
  min-width: 500px;
  height: 80px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
  background-position: bottom 20px center;
  background-size: calc(100% - 30px) 1px;
  background-repeat: no-repeat;
  background-color: #fff;
  outline: none;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.5);
}
.search-block-form.block-search .form-search--open {
  display: block;
  animation: openSearch 0.6s forwards;
}
@keyframes openSearch {
  from {
    opacity: 0;
    bottom: -150px;
  }
  to {
    opacity: 1;
    bottom: -100px;
  }
}
.search-block-form.block-search .search-form__submit {
  background-color: transparent;
  border: none;
  outline: none;
  width: 25px;
  height: 25px;
  background-size: contain;
}